                            <div class="col-sm-9">
                                <container-componente :validator="$v.observacoes">
                                    <textarea class="form-control" rows="4" placeholder="Observações sobre a alteração de status" 
                                        v-model="observacoes" @blur="$v.observacoes.$touch()"
                                    ></textarea>
                                </container-componente>
                            </div>
                        </div>

                        
                        <mensagem-componente titulo="Erro!" v-if="erroCancelar" :erros="erroCancelar" @fechar="erroCancelar = null"></mensagem-componente>
                    </div>
                    <div class="modal-footer">
                        <button type="button" class="btn btn-danger" :disabled="sucessoCancelar" @click.prevent="cancelar()"><i class="mdi mdi-reply-circle font-size-18"></i> Voltar para Andamento </button>
                        <button type="button" class="btn btn-secondary" data-dismiss="modal">  <i class="mdi mdi-close-circle font-size-18"></i> Fechar</button>                        
                    </div>
                </div><!-- /.modal-content -->
            </div><!-- /.modal-dialog -->
        </div>

        <div class="modal fade" id="modal-editar-servico" tabindex="-1" aria-labelledby="modal-editar-title" aria-hidden="true" style="display: none;">
            <div class="modal-dialog modal-dialog-scrollable modal-lg">
                <div class="modal-content">
                    <div class="modal-header">
                        <h4 class="modal-title mt-0" id="modal-cancelarTitle">  Editar Serviço</h4>
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">×</span>
                        </button>
                    </div>
                    
                    <div class="modal-body" v-if="servicoModal">
                        <div class="row">
                            <label class="col-sm-3 col-form-label">Serviço</label>
                            <div class="col-sm-9">
                                <container-componente :validator="$v.servicoModal.servico_item_id" >
                                    <select v-model="servicoModal.servico_item" class="form-control" @change="selecionouServicoItem(); $v.servicoModal.servico_item_id.$touch">
                                        <option value="" disabled >Selecione uma opção</option>
                                        <option v-for="s in servicos_item" :key="s.id"  :value="s">{{s.nome}}</option>                                    
                                    </select>
                                </container-componente>
                            </div>

                            <label class="col-sm-3 col-form-label">Descrição</label>
                            <div class="col-sm-9">
                                <input-componente
                                    v-model="servicoModal.descricao"
                                    :validator="$v.servicoModal.descricao"
                                    @blur="$v.servicoModal.descricao.$touch"
                                />
                            </div>
                            <label class="col-sm-3 col-form-label">Valor Unitário</label>
                            <div class="col-sm-9">
                                <container-componente :validator="$v.servicoModal.valor_unitario" >
                                    <money
                                        class="form-control"
                                        @blur="$v.servicoModal.valor_unitario.$touch"
                                        v-model.lazy="servicoModal.valor_unitario"
                                        v-bind="preco"
                                        @input="servicoModal.valor = servicoModal.valor_unitario * servicoModal.qtde"
                                    ></money>
                                </container-componente>
                                <span v-if="servicoModal.servico_item" style="cursor:pointer" v-popover="{ name: 'info-precos-modal' }">
                                    <span style="color: red" v-if="servicoModal.valor_unitario < servicoModal.servico_item.valor_unitario && (servicoModal.servico_item.valor_unitario_cliente <= 0 || servicoModal.valor_unitario < servicoModal.servico_item.valor_unitario_cliente )">
                                        <span class="fas fa-exclamation-triangle"></span> Valor Baixo
                                    </span>                                                                
                                    <span v-else >
                                        <span class="fas fas fa-money-bill"></span> Preço
                                    </span>
                                
                                    <popover :name="'info-precos-modal'" style="left: none; top:none; text-align: center">
                                        <p>Tabela: {{servicoModal.servico_item.valor_unitario | currency($store.state.moeda)}}</p>
                                        <p>Cliente: <span v-if="servicoModal.servico_item.valor_unitario_cliente > 0">{{servicoModal.servico_item.valor_unitario_cliente | currency($store.state.moeda)}}</span><span v-else> - </span></p>
                                        
                                    </popover>
                                </span>   
                            </div>

                            <label class="col-sm-3 col-form-label">Qtde</label>
                            <div class="col-sm-9">
                                <container-componente :validator="$v.servicoModal.qtde" >
                                    <money
                                        class="form-control"
                                        @blur="$v.servicoModal.qtde.$touch"
                                        v-model.lazy="servicoModal.qtde"
                                        v-bind="qtde((servicoModal.servico_item ? servicoModal.servico_item.medida.decimais : 2), (servicoModal.servico_item ? servicoModal.servico_item.unidade_medida : '') )"
                                        @input="servicoModal.valor = servicoModal.valor_unitario * servicoModal.qtde"
                                    ></money>
                                </container-componente>
                            </div>

                            <label class="col-sm-3 col-form-label">Subtotal</label>
                            <div class="col-sm-9">
                                <container-componente :validator="$v.servicoModal.valor" >
                                    <money
                                        class="form-control"
                                        :disabled="true"
                                        @blur="$v.servicoModal.valor.$touch"
                                        v-model.lazy="servicoModal.valor"
                                        v-bind="preco"
                                    ></money>
                                </container-componente>
                            </div>
                        </div>
                        
                    </div>
                    <div class="modal-footer row">
                        <div class="col-md-12" v-if="erroModal || sucessoModal">
                            <mensagem-componente titulo="Erro!"  :erros="erroModal" @fechar="erroModal = null"></mensagem-componente>
                            <mensagem-componente  :sucesso="sucessoModal" @fechar="sucessoModal = null"></mensagem-componente>
                        </div>
                                        
                        <div class="col-md-12 text-right">
                            <div v-if="podeEditar && servicoModal &&  servicoModal.id" style="float:left">
                                <div class="custom-control custom-switch custom-switch-lg mb-3" dir="ltr">
                                    <input type="checkbox" class="custom-control-input" id="customSwitchsizelg" v-model="queroRemover">
                                    <label class="custom-control-label" for="customSwitchsizelg">Quero Excluir</label>
                                </div>  
                            </div>
                            <button v-if="servicoModal && servicoModal.id" class="btn btn-danger" :disabled="sucessoModal || !queroRemover" @click.prevent="removerServico()"><i class="mdi mdi-trash-can font-size-18"></i> Excluir Serviço </button>
                            <button class="btn btn-primary" :disabled="sucessoModal" @click.prevent="atualizarServico()"><i class="mdi mdi-content-save font-size-18"></i> Salvar </button>
                            <button class="btn btn-secondary" data-dismiss="modal">  <i class="mdi mdi-close-circle font-size-18"></i> Fechar</button>
                        </div>
                    </div>                    
                </div>
            </div>
        </div>
    </div>
</template>

<script>

import { required, requiredIf, minValue, maxLength, email } from "vuelidate/lib/validators";
import { CaixaService, FormaPagamentoService, OrdemServicoService, ParceiroService, PlanoContasService, ServicoItemService, TipoItemOsService } from '../../services'
import moment from 'moment';

const parceiroService = new ParceiroService();
const tipoItemOsService = new TipoItemOsService();
const ordemServicoService = new OrdemServicoService();

const planoContasService = new PlanoContasService();
const caixaService = new CaixaService()
const formasPagamentoService = new FormaPagamentoService();
const servicoItemService = new ServicoItemService();

export default {
    data() {
        return {
            form: {
                id: null,
                parceiro_id: '',
                servicos: [],
                caixa: null,
                caixa_id: '',
                devolucao_data: null,
                forma_pagamento_id: '',
                com_saldo: false,
                forma_desc: ''
            },
            novo_status: '',
            observacoes: '',
            tipos_item: [],
            saldo_cliente: 0,
            parceiros: [],
            
            buscandoParceiros: false,
            
          
            sucesso: null,
            erros:null,
            errosTitulo:'',
            timer: null,

            contas: [],
            buscandoContas: false,

            erroModal: null,
            sucessoModal:null,
            observacoes_avulsas: '',

            servicoModal: null,
            queroRemover: false,
            contas: [],
            formasPagamento: [],
            sucessoCancelar: null,
            erroCancelar: null,

            servicos_item: [],
            preco:{
                decimal: ',',
                thousands: '.',
                prefix: this.$store.state.moeda + ' ',
                suffix: '',
                precision: 2,
                masked: false
            },
        }
    
    },
    validations() {
        return {

            observacoes: {required }, //: requiredIf(function(){ return this.novo_status == 'ENTREGUE' || this.novo_status == 'CANCELADO' || (this.novo_status == 'ANDAMENTO' && this.form.status == 'PENDENTE') }) },

            servicoModal:{
                ordem_servico_id: {required},
                servico_item_id: {required},
                descricao: {maxLength: maxLength(200)},
                valor: {required, minValue: minValue(0)},
                valor_unitario: {required, minValue: minValue(0.01)},
                qtde: {required, minValue: minValue(0.001)},
                aprovado: {},
            },

            form: {
                parceiro_id: {required},
                descricao: {},
                data_entrada: {},
                acessorios: {},
                avarias: {},
                observacoes: {},
                status: {},
                aprovacao_data: {},
                aprovacao_observacoes: {},
                    
                devolucao_data:{required: requiredIf(function(){ return this.form.forma_pagamento_id })},
                devolucao_observacoes: {},
                devolucao_usuario_id: {},

                valor: {},
                parcelas: [],
                fotos_entrada: {},
                fotos_saida: {},
                servicos: { required},
                forma_desc: {required: requiredIf(function(){ return this.form.caixa && this.form.caixa.conta_bancaria })}
            },
        }
    },
    
    methods: {
        submit: function(aviso = true){

            this.$v.form.$touch();
            this.erros = null;
            this.sucesso = null;    
            this.errosTitulo ='';

            if (this.$v.form.$invalid){
                this.errosTitulo = 'Preencha o formulário corretamente, alguns campos estão com erros!'
                this.erros = this.$v.form;
                return false;
            }
            this.form.valor_subtotal = this.valor_total;
            this.form.valor_total = this.valor_total;
            let obj = {...this.form};
            obj.servicos = [...this.form.servicos]
            
            let request = null;
            if(obj.id)    
                request = ordemServicoService.atualizar(obj);
            else
                request = ordemServicoService.criar(obj);

            request.then(
                response => {
                    let redireciona = !this.form.id;
                    // this.form = response.data;
                    if(aviso)
                        this.sucesso = "Registro salvo com sucesso!";
                    if(redireciona) {
                        let index = String(this.$route.path).lastIndexOf("/");
                        let newPath = String(this.$route.path).slice(0, index);
                        newPath += "/" + this.form.id;
                        this.$router.push(newPath);
                        this.buscarObjeto(this.form.id);
                    }else{
                        this.buscarObjeto(this.form.id);
                    }
                }   
            ).catch( erro => {
                if(aviso){
                    this.erros = erro 
                }else
                    console.log(erro, erro.response)
            })

        },

        abrirModalStatus: function(novo_status = ''){
            this.sucessoModal = null;
            this.erroModal = null;
            this.novo_status = novo_status;
            this.observacoes = '';
            this.$v.observacoes.$reset();
        },

        atualizarStatus: function(){
            
            this.sucessoModal = null;
            this.erroModal = null;

            if(this.$v.observacoes.$invalid){
                this.erroModal = "É necessário preencher o campo de observações!";
                this.$v.observacoes.$touch();
                return false;
            }

            let obj = {
                id: this.form.id,
                status: this.novo_status,
                observacoes: this.observacoes
            };

            ordemServicoService.atualizarStatus(obj).then(response => {
                this.form.status = this.novo_status;
                this.novo_status = '';
                this.observacoes = '';
                this.sucessoModal = 'Status atualizado com sucesso!!!';
                this.$v.observacoes.$reset();
                this.form.observacoes = response.data.observacoes;
            }).catch( error => {
                this.erroModal = error;
            });
            
        },

        podeTrocarStatus: function(novo_status){
            let status_nao_permitidos = [];
            if(this.form.status == novo_status)
                return false
            switch(this.form.status){
                case 'PENDENTE' :
                    status_nao_permitidos = ['AGUARDANDO PECA','ANDAMENTO','AGUARDANDO RETIRADA','ENTREGUE'];
                    return status_nao_permitidos.indexOf(novo_status) < 0;
                case 'ORCAMENTO' :
                    status_nao_permitidos = ['AGUARDANDO RETIRADA','ENTREGUE','AGUARDANDO PECA'];
                    return status_nao_permitidos.indexOf(novo_status) < 0;
                case 'CANCELADO' :
                    status_nao_permitidos = ['AGUARDANDO RETIRADA','ORCAMENTO','AGUARDANDO PECA','ANDAMENTO','ENTREGUE'];
                    return status_nao_permitidos.indexOf(novo_status) < 0;
                case 'AGUARDANDO PECA' :
                    status_nao_permitidos = ['PENDENTE','CANCELADO','ENTREGUE'];
                    return status_nao_permitidos.indexOf(novo_status) < 0;
                case 'ANDAMENTO' :
                    status_nao_permitidos = ['PENDENTE','ORCAMENTO','ENTREGUE'];
                    return status_nao_permitidos.indexOf(novo_status) < 0;
                case 'AGUARDANDO RETIRADA' :
                    status_nao_permitidos = ['PENDENTE','ANDAMENTO','ORCAMENTO','AGUARDANDO PECA','ANDAMENTO','ENTREGUE'];
                    return status_nao_permitidos.indexOf(novo_status) < 0;
                case 'SAIU PARA ENTREGA' :
                    status_nao_permitidos = ['PENDENTE','ANDAMENTO','ORCAMENTO','AGUARDANDO PECA','ANDAMENTO','ENTREGUE'];
                    return status_nao_permitidos.indexOf(novo_status) < 0;
                case 'ENTREGUE' :
                    status_nao_permitidos = ['PENDENTE', 'ORCAMENTO', 'CANCELADO','AGUARDANDO PECA', 'ANDAMENTO', 'AGUARDANDO RETIRADA', 'ENTREGUE'];
                    return status_nao_permitidos.indexOf(novo_status) < 0;
            }
        },

        novaObservacaoAvulsa: function(){
            let obj = {
                id: this.form.id,
                observacoes: this.observacoes_avulsas
            }
            ordemServicoService.observacoes(obj).then( response => {
                this.form.observacoes = response.data.observacoes;
                this.observacoes_avulsas = '';
                Vue.$toast.success(`Observação registrada com sucesso!`)
            }).catch( error => {
                console.log(error, error.response);
                Vue.$toast.error(`Não foi possível registrar esta observação!`)
            })

        },

        abrirModalServico: function(servico = null){
                        
            this.erroModal = null;
            this.sucessoModal = null;
            this.queroRemover = false;
            this.$v.servicoModal.$reset()
            if(servico == null){
                this.servicoModal = {
                    servico_item_id: '',
                    ordem_servico_id: this.form.id,
                    valor: 0,
                    valor_unitario: 0,
                    qtde: 0,
                    descricao: ''
                }
            }else{                
                servico.valor_unitario = Number(servico.valor_unitario)
                servico.qtde = Number(servico.qtde)
                servico.valor = Number(servico.valor)
                let i = this.servicos_item.map(x => x.id).indexOf(servico.servico_item_id)
                servico.servico_item = this.servicos_item[i]

                this.servicoModal = {...servico}
                this.selecionouServicoItem();
            }
            
            this.$forceUpdate()
        },

        atualizarServico: function(){
            this.erroModal = null;
            this.sucessoModal = null;

            this.$v.servicoModal.$touch()
            if(this.$v.servicoModal.$invalid){
                this.erroModal = this.$v.servicoModal
                return 0
            }

            let obj = {...this.servicoModal};
            obj.aprovado = 'APROVADO';            
            
            ordemServicoService.atualizarServico(obj).then(response => {
                this.sucessoModal = "Serviço atualizado com Sucesso!!!";
                let obj = response.data;
                let index = this.form.servicos.map(x => x.id).indexOf(obj.id)                
                obj.novo_valor = Number(obj.valor);
                obj.valor = Number(obj.valor);
                obj.novo_aprovado = obj.aprovado;
                obj.erros = null;
                obj.sucesso = null;
                if(index < 0){
                    this.form.servicos.push(obj)
                }else{
                    this.form.servicos.splice(index,1,obj);
                }
                this.submit(false);
            }).catch( error => {
                this.erroModal = error;
            }).finally( () => this.$forceUpdate())
            
        },

        removerServico:function(){
            this.erroModal = null;
            this.sucessoModal = null;

            if(this.form.servicos.length <= 1){
                this.erroModal = "Este é o último serviço da ordem e não pode ser excluído";
                console.log(erroModal)
                return false;
            }
            
            
            ordemServicoService.removerServico(this.servicoModal).then(response => {
                this.sucessoModal = "Serviço removido com Sucesso!!!";
                
                this.form.servicos = this.form.servicos.filter(x => x.id != this.servicoModal.id)                

                this.submit(false);
            }).catch( error => {
                this.erroModal = error;
            }).finally( () => this.$forceUpdate())
        },

        qtde: function(decimais, suffix){
            return {
                decimal: ',',
                thousands: '.',
                prefix: '',
                suffix: ' ' + suffix,
                precision: decimais,
                masked: false
            }
        },
        buscarObjeto: function(id){
            ordemServicoService.buscar(id)
                .then(  response => {
                    this.form = response.data;
                    this.form.servicos = this.form.servicos.map(x => {
                        x.novo_valor = Number(x.valor);
                        x.valor = Number(x.valor);
                        x.novo_aprovado = x.aprovado;
                        x.erros = null;
                        x.sucesso = null;
                        return x;
                    })
                    
                }).catch( erro => this.erros = erro)
        },

        buscarParceirosDebounce: function(text, loading){
            clearTimeout(this.timer);
            this.timer = setTimeout(() => {
                this.buscarParceiros(text, loading)
            }, 700);
        },

        buscarParceiros: function(text, loading){
            this.buscandoParceiros = true;
            parceiroService.listar({texto: text, per_page: 50, id: this.form.id, tipo: 'CLIENTE'})
                .then( response =>  this.parceiros = response.data.data )
                .catch( error => console.log(error))
                .finally( () => this.buscandoParceiros = false)
        },
       
        desabilitaDataFutura: function(data) {
            let hoje = new Date();
            return data > hoje
        },

        buscarTiposItem: function(){
            
            tipoItemOsService.listar({per_page: 2000, })
                .then( response =>  {
                    this.tipos_item = response.data.data;
                    if (this.$route.params.id > 0) {
                        this.buscarObjeto(this.$route.params.id);
                    }

                } )
                .catch( error => console.log(error))
        },

        imprimir:function(tipo){
            switch(tipo){
                case 'entrada': 
                    return ordemServicoService.imprimirEntrada(this.form.id)
                case 'entrega': 
                    return ordemServicoService.imprimirEntrega(this.form.id)
                case 'orcamento': 
                    return ordemServicoService.imprimirOrcamento(this.form.id)
                case 'laudo': 
                    return ordemServicoService.imprimirLaudo(this.form.id)
            }

        },

        buscarServicosItem: function(){
            servicoItemService.listar({ativo: 1, per_page:1000}).then( response => {
                this.servicos_item = response.data.data.map(x => {
                    x.valor_unitario = Number(x.valor_unitario);
                    return x;
                });
            }).catch(error => {
                console.log('Erro ao buscar Serviços Itens: ', error, error.response);
                setTimeout(() => {
                    this.buscarServicosItem();
                }, 2000);
            })
        },

        selecionouServicoItem:function(){
            setTimeout(() => {
                this.servicoModal.servico_item_id = this.servicoModal.servico_item.id;
                let valor_unitario = this.servicoModal.servico_item.valor_unitario;
                if(this.cliente_tem_tabela()){                    
                    valor_unitario = this.valor_unitario_cliente();
                    this.servicoModal.valor_unitario = this.servicoModal.valor_unitario == 0 ? valor_unitario : this.servicoModal.valor_unitario;
                    this.servicoModal.valor_selecionado = 'Cliente';
                    this.servicoModal.servico_item.valor_unitario_cliente = valor_unitario;
                }else{
                    this.servicoModal.valor_selecionado = 'Tabela';
                    this.servicoModal.valor_unitario = this.servicoModal.valor_unitario == 0 ? valor_unitario : this.servicoModal.valor_unitario;
                    this.servicoModal.servico_item.valor_unitario_cliente = 0;
                }                
                
            }, 10);
        },


        cliente_tem_tabela: function(){
            
            if(!this.form.parceiro || !this.form.parceiro.tabela_servicos)
                return false;

            let i = this.form.parceiro.tabela_servicos.map(x => x.id).indexOf(this.servicoModal.servico_item_id) 
            if(i < 0)
                return false
            let servico_cliente = this.form.parceiro.tabela_servicos[this.form.parceiro.tabela_servicos.map(x => x.id).indexOf(this.servicoModal.servico_item_id)];
            return servico_cliente.pivot.valor_unitario > 0
        },

        valor_unitario_cliente: function(){
            if(!this.form.parceiro || !this.form.parceiro.tabela_servicos)
                return -1;

             let servico_cliente = this.form.parceiro.tabela_servicos[this.form.parceiro.tabela_servicos.map(x => x.id).indexOf(this.servicoModal.servico_item_id)];
             
             if(servico_cliente.pivot.valor_unitario > 0){
                return servico_cliente.pivot.valor_unitario
             }
                
            return servico_cliente.valor_unitario
        },

        buscarTabelaServicosCliente: function(){
            setTimeout(() => {
                parceiroService.buscarTabelaServicos(this.form.parceiro_id).then( response =>{
                    this.form.parceiro.tabela_servicos = response.data.map(x => {
                        x.pivot.valor_unitario = Number(x.pivot.valor_unitario)
                        x.valor_unitario = Number(x.valor_unitario)
                        return x
                    })
                })    
            }, 800);
            
        },


        // Área da Finalização da OS

        abrirModalFinalizar: function(){
            setTimeout(() => {
                    this.buscarSaldoCliente();
                }, 50);

            if(this.contas.length == 0)
                setTimeout(() => {
                    this.buscarContas();
                }, 150);


            if(this.formasPagamento.length == 0)
                setTimeout(() => {
                    this.buscarFormasPagamento();
                }, 100);
            
            this.form.parcelas = [];
            this.erros = null;
            this.successo = null;
            this.form.devolucao_data = new Date();
        },

        buscarSaldoCliente(){
            this.saldo_cliente = 0;
            caixaService.buscarSaldoParceiro(this.form.parceiro.id)
                .then(response => {
                    this.saldo_cliente = response.data[0].saldo
                    if(this.saldo_cliente < 0)
                        Vue.$toast.error(`Atenção! Este cliente está com saldo negativo!`);

                    if(this.saldo_cliente > 0)
                        Vue.$toast.success(`Cliente com saldo disponível!`);
                })
                .catch(error => {
                    Vue.$toast.error(`Não foi possível carregar o saldo do parceiro`);
                    console.log("Erro ao buscar saldo do parceiro", error, error.response);
                    setTimeout(() => {
                        this.buscarSaldo();
                    }, 2000);
                })
        },

        buscarFormasPagamento: function(){
            formasPagamentoService.listar({ativo: true, per_page:1000}).then( response => this.formasPagamento = response.data.data);
        },

        buscarContas:function(){            
            this.buscandoContas = true;
            planoContasService.listar({tipo: "TRANSFERENCIA", per_page: 50}).then(
                response => {
                    this.contas = response.data.data.map(x => {
                        return {
                            id: x.id, 
                            label: `${x.codigo} | ${x.nome}`, 
                            banco: x.banco
                        }
                    });
                    
            }).finally( () => this.buscandoContas = false)
        },

        selecionarFormaPagamento: function(forma){
            if(forma == 'credito_futuro'){
                this.form.formaPagamento = {id: -1, nome: 'Crédito', descricao: 'Cobrança Futura', vista: true};
                this.form.forma_pagamento_id = -1
            }else{
                this.form.formaPagamento = forma;
                this.form.forma_pagamento_id = forma.id;
            }            

            if(forma && forma.caixa){
                this.form.caixa = forma.caixa;
                this.form.caixa_id = forma.caixa.id;
            }else{
                delete this.form.caixa ; //= null;
                delete this.form.caixa_id ; //= '';
            }

            this.gerarParcelas();
            
        },

        gerarParcelas: function(){
            if(!this.form.formaPagamento.vista || !this.form.formaPagamento.compensa_automatico || this.form.formaPagamento.dias > 0){
                if(!this.form.qtde_parcelas)
                    this.form.qtde_parcelas = 1;

                let valor = Number((this.valor_total / this.form.qtde_parcelas).toFixed(2));
                let soma_valor = 0;
                
                this.form.parcelas = [];                
                let vencimento = moment().add(this.form.formaPagamento.dias, 'days');
                for (let i = 1; i <= this.form.qtde_parcelas; i++){

                    this.form.parcelas.push(
                        {
                            parcela: this.form.formaPagamento.vista || this.form.formaPagamento.entrada ? i-1 : i,
                            parcelas: this.form.qtde_parcelas,
                            valor: i != this.form.qtde_parcelas ? valor : Number((this.valor_total - soma_valor).toFixed(2)),
                            data_vencimento: vencimento.toDate()
                        }  
                    );
                    soma_valor += valor;
                    vencimento = vencimento.add(1, 'months');
                }
                

            }else{
                this.form.qtde_parcelas = 0;
                this.form.parcelas = null;
            }

            this.$forceUpdate();
            

        },

        finalizar: function(){

            this.erros = null;
            this.successo = null;
            this.form.valor_subtotal = this.valor_total;
            this.form.valor_total = this.valor_total;
            
            if(this.form.caixa && !this.form.caixa.conta_bancaria){
                this.form.forma_desc = ''
            }

            ordemServicoService.entregarEquipamento(this.form).then(response => {
                this.form.valor_subtotal = response.data.valor_subtotal;
                this.form.valor_acrescimo = response.data.valor_acrescimo;
                this.form.valor_desconto = response.data.valor_desconto;
                this.form.valor_total = response.data.valor_total;
                this.form.status = response.data.status;
                this.form.devolucao_data = response.data.devolucao_data;
                this.form.devolucao_observacoes = response.data.devolucao_observacoes;
                this.form.devolucao_usuario_id = response.data.devolucao_usuario_id;
                
                this.sucesso = "Operação realizada com sucesso!";
            }).catch(
                error => this.erros = error,
            )
        },

        buscarContasDebounce: function(){
            clearTimeout(this.debounce);
            this.debounce = setTimeout(() => {
                this.buscarContas();
            }, 800);
        },
        
        buscarContas:function(){            
            this.buscandoContas = true;
            planoContasService.listar({tipo: "TRANSFERENCIA", per_page: 50}).then(
                response => {
                    this.contas = response.data.data.map(x => {
                        return {
                            id: x.id, 
                            label: `${x.codigo} | ${x.nome}`, 
                            banco: x.banco
                        }
                    });
                    
            }).finally( () => this.buscandoContas = false)
        },

        cancelar: function(){
           
            this.erroCancelar = null;
            this.sucessoCancelar = null;    
            this.errosTitulo ='';
            if(this.$v.observacoes.$invalid){
                this.erroModal = "É necessário preencher o campo de observações!";
                this.$v.observacoes.$touch();
                return false;
            }
            let obj = {id: this.form.id, observacoes: this.observacoes};
            
            let request = ordemServicoService.cancelar(obj);

            request.then(
                response => {
                    //Data de Entrega
                    this.form.status = response.data.status;
                    this.sucessoCancelar = "OS está em andamento novamente!";
                    
                }   
            ).catch( erro => this.erroCancelar = erro )
        },

        desabilitaDataFutura: function(data) {
            let hoje = new Date();
            return data > hoje
        },

    },
    mounted() {

        // if (this.$route.params.id > 0) {
        //     setTimeout(() => {
        //         this.buscarObjeto(this.$route.params.id);    
        //     }, 500);
        // }

        setTimeout(() => {
            this.buscarTiposItem();
        }, 50);

        setTimeout(() => {
            this.buscarServicosItem();
        }, 50);

        
    },
    computed:{
        editando(){
            return this.$route.params.id > 0;
        },
        podeAdicionarServico(){
            return !this.editando || true;
        },
        podeEditar(){
            // 'AGUARDANDO PECA','ANDAMENTO'
            let x = ['ENTREGUE','CANCELADO'];
            return x.indexOf(this.form.status) < 0
        },

        itens_por_tipo(){
            let itens = this.form.servicos.filter(x => x.servico_item_id != null ).map(x => x.servico_item_id);
            console.log("Itens 1208", itens)
            itens = [...new Set(itens)];
            console.log("Itens 1210", itens)
            
            itens = itens.map(x => {                
                let itens_total = this.form.servicos.filter(i => i.servico_item_id == x);
                console.log("Itens Total 1212", x , itens_total)
                let ret = {
                    servico_item : itens_total[0].servico_item,
                    qtde_total : itens_total.reduce( (a,c) => Number(a) + Number(c.qtde), 0),
                    valor_total : itens_total.reduce( (a,c) => Number(a) + Number(c.valor), 0),
                };
                return ret;
            })

            return itens
        },

        valor_total(){
            if(!this.form.servicos)
                return 0
            return this.form.servicos.reduce((a,c) => Number(a) + Number(c.valor), 0)
        }
    }

   
};
</script>
